import React from 'react'

class About extends React.Component{
    constructor(){
        super();
        this.state={collapse:false};
    }

    toggle(){
        this.setState({
            collapse:!this.state.collapse
        });
    }

    render() {
       return (
           <section id="about">
    			<div className="container-fluid">
    				<div className="animate-box section-header" data-animate-effect="fadeInLeft">
    					<span className="font-note">About</span>
    					<h2 className="header">What is D3.js</h2>
    				</div>
                    <div className="container-fluid">
                        <p>
                        D3.js is a JavaScript library for manipulating documents based on data. 
                        D3 helps you bring data to life using HTML, SVG, and CSS. 
                        D3â€™s emphasis on web standards gives you the full capabilities of modern browsers without tying yourself to a proprietary framework, 
                        combining powerful visualization components and a data-driven approach to DOM manipulation.
                        </p>
                    </div>
    			</div>
    		</section>
       )
    }
}
export default About